import contactStyles from "../contact/index.module.css";

const Contact = () => {
  const handleClick = (a) => {
    a.preventDefault();
    let y = document.getElementsByTagName("input");
    let z = document.getElementsByTagName("textarea");
    console.log(y);
    for (let i = 0; i < y.length - 1; i++) {
      if (y[i] === "") {
        alert("please fill all the fields");
        return;
      }
    }
    if (z[0].value === "") {
      alert("please fill all the fields");
      return;
    }
    if(!Array.from(y[1].value).includes('@'))
    {
      alert('invalid email');
      return;
    }
    if (y[2].value.length !== 10) {
      alert("enter valid mobile no.");
      return;
    }
    for (let i = 0; i < y.length - 1; i++) y[i].value = "";
    z[0].value='';
  };
  return (
    <div className={contactStyles.formContainer}>
      <h1>Contact Us</h1>
      <form>
        <div>
          <input className={contactStyles.inp} placeholder="Enter Your Name" />
        </div>
        <div>
          <input className={contactStyles.inp} type="email" placeholder="Enter Your Email" />
        </div>
        <div>
          <input className={contactStyles.inp} type="number" placeholder="Enter Your Mobile no." />
        </div>
        <div>
          <textarea className={contactStyles.inp} rows="10" placeholder="Enter Your Message" />
        </div>
        <div>
          <input
            className={contactStyles.button}
            type="submit"
            value="submit"
            onClick={(e) => handleClick(e)}
          />
        </div>
      </form>
    </div>
  );
};

export default Contact;
