import productStyles from "../products/index.module.css";
import { players } from "../data/data";
import { useEffect, useState } from "react";
const Product = () => {
  const [tableData, settableData] = useState([]);
  const [ren, setren] = useState({ first: false, second: false, third: true });

  useEffect(() => {
    let y = document.getElementsByClassName("inpChk");
    y[0].checked = true;
    y[1].checked = true;
    for (let i = 0; i < players.length; i++) players[i].id = i + 1;
    settableData(players.slice(0, 12));
  }, []);

  const handelNext = () => {
    document.getElementById("previous").disabled = false;
    let lastCell = document.getElementsByTagName("tr");
    let lastData = parseInt(lastCell[11].cells[0].firstChild.data);
    lastData == 96
      ? (document.getElementById("next").disabled = true)
      : settableData(players.slice(lastData , lastData + 12));
  };

  const handelPrevious = () => {
    document.getElementById("next").disabled = false;
    let firstCell = document.getElementsByTagName("tr");
    let firstData = parseInt(firstCell[0].cells[0].firstChild.data);
    firstData == 1
      ? (document.getElementById("previous").disabled = true)
      : settableData(players.slice(firstData - 13, firstData - 1));
  };

  const HandleChecked = () => {
    let y = document.getElementsByClassName("inpChk");
    if ((y[0].checked && !y[1].checked) || (y[1].checked && !y[0].checked))
      y[0].checked && !y[1].checked
        ? setren({ first: true, second: false, third: false })
        : setren({ first: false, second: true, third: false });
    else setren({ first: false, second: false, third: true });
  };

  return (
    <div className={productStyles.ProductDiv}>
      <div className={productStyles.tableContainer}>
        <table className={productStyles.table}>
          <tbody>
            {console.log("entering table")}
            {ren.first &&
              tableData.map((item) => <DisplayWithTeam {...item} />)}
            {ren.second &&
              tableData.map((item) => <DisplayWithRole {...item} />)}
            {ren.third && tableData.map((item) => <DisplayAll {...item} />)}
          </tbody>
        </table>
        <div className={productStyles.btnCont}>
          <button onClick={() => handelPrevious()} id="previous">
            Previous
          </button>
          <button onClick={() => handelNext()} id="next">
            Next
          </button>
        </div>
      </div>
      <div className={productStyles.chkCont}>
        <div className={productStyles.chkcontSub}>
          <h4>CHOOSE OPTIONS</h4>
          <div>
            <label>
              <input
                class="inpChk"
                type="checkbox"
                onClick={() => HandleChecked()}
              ></input>
              <span>Teams</span>
            </label>
            <br />
            <label>
              <input
                class="inpChk"
                type="checkbox"
                onClick={() => HandleChecked()}
              ></input>
              <span>Role</span>
            </label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Product;


const DisplayAll = ({ id, name, Role, team }) => {
  return (
    <tr className={productStyles.row}>
      <td>{id}</td>
      <td>{name}</td>
      <td>{Role}</td>
      <td>{team}</td>
    </tr>
  );
};

const DisplayWithTeam = ({ id, name, Role, team }) => {
  return (
    <tr className={productStyles.row}>
      <td>{id}</td>
      <td>{name}</td>
      <td>{team}</td>
    </tr>
  );
};

const DisplayWithRole = ({ id, name, Role, team }) => {
  return (
    <tr className={productStyles.row}>
      <td>{id}</td>
      <td>{name}</td>
      <td>{Role}</td>
    </tr>
  );
};
