import cardStyles from '../news-cards/index.module.css'

const Card = ({
    urlToImage,
    author,
    title,
    description,
    publishedAt,
    url
}) => {
    return ( 
        <div className={cardStyles.newsCard}>
            <div><img style={{width:'100%',maxHeight:'200px',borderRadius:'10px'}} src={urlToImage} /></div>
            <h3> {title}</h3>
            <p>{description}</p><button className={cardStyles.read} onClick={()=>window.open(url)} >read more...</button>
            <h3>{author}</h3>
            <h4>{publishedAt}</h4>
        </div>
     );
}
 
export default Card;